<template>
<div>
    <div class="triangle"></div>
    <v-icon @mouseover="mouseOver" @mouseout="mouseOut" color="#f44336" >{{active ? "favorite" : "favorite_border"}}</v-icon>
</div>
    
</template>

<script>
export default {
  data() {
    return {
      favorite: false,
      hovered: false
    };
  },
  computed: {
    active() {
      return this.favorite || this.hovered;
    }
  },
  methods: {
    mouseOver() {
      this.hovered = !this.hovered;
    },
    mouseOut() {
      this.hovered = !this.hovered;
    }
  }
};
</script>

<style lang="sass" scoped>
    $triangle-size: 40px
    .triangle
      cursor: pointer
      height: 0px
      width: 0px
      border-top: $triangle-size solid #fff
      border-left: $triangle-size solid #fff
      border-right: $triangle-size solid transparent
      border-bottom: $triangle-size solid transparent
      position: relative
    .v-icon
      font-size: 40px
      font-weight: bold
      position: absolute 
      cursor: pointer
      margin: 5px
      top: 0
      left: 0
</style>
